@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Spartan", sans-serif;
}

body {
  font-family: "Barlow", sans-serif;
}

.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.container .hero {
  width: 100%;
  display: flex;
  align-items: stretch;
  flex-direction: row;
  width: 100%;
}
.container .hero .left-content {
  position: relative;
  width: 60%;
}
.container .hero .left-content .item {
  width: 100%;
  object-fit: cover;
  object-position: center;
  animation-name: fade;
  animation-duration: 1.5s;
}
@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.container .hero .left-content .item .slide_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.container .hero .left-content .navbar {
  position: absolute;
  top: 10%;
  left: 5%;
  display: flex;
  align-items: center;
  background-color: transparent;
}
.container .hero .left-content .navbar .navbar__hamburger {
  display: none;
}
.container .hero .left-content .navbar .navbar__logo {
  margin-right: 5rem;
  display: inline-block;
  cursor: pointer;
}
.container .hero .left-content .navbar .navbar__lists {
  list-style-type: none;
  display: flex;
}
.container .hero .left-content .navbar .navbar__lists .navbar__lists--item {
  margin-right: 2rem;
}
.container .hero .left-content .navbar .navbar__lists .navbar__lists--item .navbar__link {
  color: #fff;
  text-decoration: none;
  text-transform: lowercase;
  font-size: 1.8rem;
}
.container .hero .right-content {
  width: 40%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 2rem;
}
.container .hero .right-content .cta__wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container .hero .right-content .cta__wrapper .cta__header {
  font-size: 3rem;
}
.container .hero .right-content .cta__wrapper .cta__text {
  margin-bottom: 2rem;
  color: #a1a1a1;
  line-height: 1.5;
  font-size: 1.3rem;
}
.container .hero .right-content .cta__wrapper .cta__btn {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.container .hero .right-content .cta__wrapper .cta__btn .cta__shopnow {
  margin-right: 2rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #000;
  text-decoration: none;
  transition: all 0.2s ease;
  font-size: 1.5rem;
}
.container .hero .right-content .navigation {
  background-color: #000;
  margin-top: 2rem;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  width: 40%;
  max-width: 160px;
  min-width: 160px;
  margin-left: -2rem;
  margin-bottom: -0.8rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.container .hero .right-content .navigation .navigation__icon {
  height: 2.4rem;
  width: 1.4rem;
  cursor: pointer;
}
.container .footer {
  display: flex;
  flex-direction: row wrap;
}
.container .footer .about__info {
  height: 100%;
  padding: 0 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.container .footer .about__heading {
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  font-size: 1.4rem;
  letter-spacing: 3px;
}
.container .footer .about__text {
  line-height: 1.5;
  color: #a1a1a1;
}

@media screen and (max-width: 1012px) {
  .container .hero {
    flex-direction: column;
  }
  .container .hero .left-content {
    width: 100%;
  }
  .container .hero .left-content .navbar .navbar__logo {
    display: none;
  }
  .container .hero .left-content .navbar .navbar__hamburger {
    display: block;
    font-size: 2.5rem;
    margin-right: 2rem;
  }
  .container .hero .left-content .navbar .navbar__lists {
    display: none;
  }
  .container .hero .right-content {
    width: 100%;
  }
  .container .hero .right-content .cta__wrapper .cta__btn {
    padding-bottom: 3rem;
  }
  .container .hero .right-content .navigation {
    position: absolute;
    top: -85px;
    right: 0;
    padding-bottom: 0.9rem;
    padding-top: 0.9rem;
  }

  .footer {
    flex-direction: column;
    width: 100%;
  }
  .footer .about__img {
    width: 100%;
    object-fit: cover;
  }
  .footer .about__wrapper {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .footer .about__wrapper .about__info .about__heading {
    font-size: 2rem;
  }
  .footer .about__wrapper .about__info .about__text {
    font-size: 1.4rem;
  }
}